// import productImg from "../../assets/product.webp";
import IndexNavbar from "../../components/Navbar/IndexNavbar";
import ProuductItem from "../../components/product/ProuductItem";

interface Props {}

const ShopPage = (props: Props) => {
  // const imgUrl = "https://via.placeholder.com/150";
  const imgUrl =
    "https://images.unsplash.com/photo-1583121274602-3e2820c69888?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=1050&q=80";
  let productList = [];

  for (let i = 0; i < 30; i++) {
    productList.push(`Product ${i}`);
  }

  return (
    <>
      <IndexNavbar />

      <div id="particles-js" className="mt-20 flex justify-between px-5 py-4">
        <div
          className="bg-green-700 w-1/6 h-2/3 sticky hidden md:block "
          style={{ top: "10%" }}
        >
          <h3>Sticky sidebar</h3>
          <p>I will follow you!</p>
          <p>
            <a href="https://caniuse.com/#search=sticky">caniuse stats</a>
          </p>
        </div>

        <main className="h-full w-full  md:ml-16 flex flex-col">
          <div className=" flex flex-col pb-24 justify-center items-center space-y-4 md:space-y-0 md:grid md:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-x-6 gap-y-8 ">
            {productList.map((p) => (
              <ProuductItem key={p} imgUrl={imgUrl} />
            ))}
          </div>
        </main>
      </div>
    </>
  );
};

export default ShopPage;
